import React, { Component } from "react";
import TextField from '@material-ui/core/TextField';

class Log_in extends Component {
	constructor(props) {
		super(props);
		this.state = {value: ''};

		this.handleChange = this.handleChange.bind(this);
		this.handleSubmit = this.handleSubmit.bind(this);
		}

		handleChange(event) {
		this.setState({value: event.target.value});
		}

		handleSubmit(event) {
		alert('A name was submitted: ' + this.state.value);
		event.preventDefault();
		}

		render() {
		return (
			<div>
			<form onSubmit={this.handleSubmit}>
				<TextField id="outlined-basic" style={{position:"absolute", top:500, left:400}} label="Username" variant="outlined" />
				
			</form>
			<form>
			<TextField id="outlined-basic" style={{position:"absolute", top:600, left:400}} label="Password" variant="outlined" />
			</form>
			<input type="submit" value="Submit" style={{position:"absolute", top:700, left:400}}/>
			
			</div>
		);
	}
}


export default Log_in;
